@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);

* {
  font-family: "Century Gothic"; }

html {
  scroll-behavior: smooth;
  font-size: 1.1vw; }

 ::placeholder {
  color: #9d9d9d;
  font-weight: 500; }

body {
  margin: 0 0;
  padding: 0 0;
  overflow-x: hidden;
  max-width: 100vw; }

.background {
  background-repeat: no-repeat;
  background-size: contain;
  padding: 0;
  margin: 0; }

img {
  width: auto;
  height: auto;
  display: inline-block; }

.header {
  position: relative; }

.image {
  width: 100%; }

div {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  user-select: none;
  color: #757575; }

ul {
  list-style-type: none;
  display: inline-block;
  margin: 0; }

.logo {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 5.5vw;
  width: 6.7vw; }

.sign_btn {
  position: absolute;
  display: inline-block;
  top: 1.3vh;
  right: 3.7vw;
  width: 6.5vw; }

.main_nav {
  margin: 1.6% 0 2.6% 0;
  text-align: center; }

li {
  display: inline-block;
  margin: 0 0 0 5%;

  a:active {
    font-weight: bold;
    color: #000000; } }

.menu {
  width: 60%;
  font-size: 1rem;
  padding: 1.1% 0 0 0; }

.home {
  position: relative;
  top: 0;
  left: 0; }

#slogan {
  position: absolute;
  top: 35%;
  left: 35%;
  width: 28%;
  animation-duration: 2s;
  animation-name: zoom_in;
  animation-fill-mode: forwards; }

#search_bar {
  position: absolute;
  top: 77%;
  left: 29%;
  width: 71%; }

input {
  font-size: 1.1rem;
  padding: 1.3% 0 1.3% 4.3%;
  width: 50%;
  border: 2px solid #ccc; }

.loc_icon {
  position: absolute;
  padding: 1.25% 0 0 1.8%;
  width: 1.7%; }

.find_btn {
  position: absolute;
  padding: 0 0 0 1.3%;
  height: 95%; }

p {
  font-family: "Noto Sans CJK TC Medium Medium","微軟正黑體";
  width: 200px;
  height: 200px;
  text-align: justify;
  text-justify: inter-ideograph; }
